@import url(https://fonts.googleapis.com/css?family=Raleway&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: #f7f7f2;
}


.nav-link {
    font-family: 'Raleway', sans-serif;
    margin: 25px 5px;
    display: inline;
    color: #f7f7f2;
    font-size: 22px;
    text-decoration: none;
}

.nav-link:hover {
    color: #0f0f0f;
    cursor: pointer;
}
#nav-logo {
    font-size: 35px;
    color: #0f0f0f;
    display: inline;
    margin: auto 20px;
}
#nav-wrap {
    text-align: center;
    padding: 20px;
    background-color: #8757cf;
    z-index: 5;
    position: fixed;
    top: 0;
    width: 100%;
}
#headshot {
    width: 250px;
    border-radius: 4px;
    -webkit-transform: rotate(270deg);
            transform: rotate(270deg);
    float: left;
    position: relative;
    top: 50px;
}

#about {
    float: right;
    position: relative;
    height: 400px;
}



.social-img {
    height: 50px;
    width: 50px;
    margin: 10px;
}

.social-img:hover {
    cursor: pointer;
    opacity: .5;
}

#contact {
    text-align: center;
}

#social-wrapper {
    padding: 20px;
}

#copyright {
    font-size: 12px;
}

#content-area {
    /* overflow: auto; */
    margin: 10px;
    padding: 15px 50px;
    font-family: 'Open Sans', sans-serif;
    background-color: #f7f7f2;

    position: absolute;
    top: 70px;
    overflow-y:scroll; 
}

.motivation {
    width: 100%;
}

.content-section {
    display: none;
}

.active-area {
    display: block;
}

h1 {
    text-align: center;
}
